<template>
  <transition>
    <li>
    <label>
      <input
        type="checkbox"
        :checked="todo.done"
        @change="handlecheck(todo.id)"
      />
      <span v-show="!todo.isEdit">{{ todo.title }}</span>
      <input
        type="text"
        v-show="todo.isEdit"
        :value="todo.title"
        @blur="handleBlur(todo, $event)"
        ref="inputTitle"
      />
    </label>
    <button class="btn btn-edit" @click="handleEdit(todo)">编辑</button>
    <button class="btn btn-danger" @click="removeItem(todo.id)">删除</button>
  </li>
  </transition>
  
</template>

<script>
export default {
  name: "MyItem",
  // 声明接受对象
  // props:['todo','checktodo','deleteItem','checkAll'],
  props: ["todo", "checkAll"],
  methods: {
    handlecheck(id) {
      // this.checktodo(id)
      this.$bus.$emit("checktodo", id);
    },
    removeItem(id) {
      // this.deleteItem(id)
      this.$bus.$emit("deleteItem", id);
    },
    handleEdit(todo) {
      //先判断todo对象是不是本来就有这个属性
      if (todo.hasOwnProperty.call(todo, "isEdit")) {
        todo.isEdit = true;
      } else {
        //给todo 对象 追加属性
        this.$set(todo, "isEdit", true);
      }
      //在点击编辑后立即获取input的焦点
      this.$nextTick(function () {
        this.$refs.inputTitle.focus();
      });
    },

    handleBlur(todo, e) {
      todo.isEdit = false;
      //事件总线上添加
      // console.log('updateItem',todo.id,e.target.value);
      this.$bus.$emit("updateItem", todo.id, e.target.value);
    },
  },
};
</script>

