<template>
      <div class="todo-header">
        <input type="text" placeholder="请输入你的任务名称，按回车键确认" @keyup.enter="add"/>
      </div>
</template>

<script>
  import {nanoid} from 'nanoid'
export default {
  name: 'MyHeader',
  props:["receive"],
  methods: {
    add(e){
      // 将用户输入的内容包装成一个组件对象
      const todoObj={id:nanoid(),title:e.target.value,done:false}
      this.receive(todoObj)
    }
  },  
  
}
</script>

