<template>
  <body>
  <div id="root">
    <div class="todo-container">
      <div class="todo-wrap">
        <MyHeader :receive="receive"></MyHeader>     
        <!-- <MyList :todos="todos" :checktodo="checktodo" :deleteItem="deleteItem" ></MyList> -->
        <MyList :todos="todos" ></MyList>
        <MyFooter :todos="todos" :checkAll="checkAll" :removeAllDone="removeAllDone"></MyFooter>
      </div>
    </div>
  </div>


  </body>
</template>

<script>
import MyFooter from './components/MyFooter.vue'
import MyHeader from './components/MyHeader.vue'
import MyList from './components/MyList.vue'



export default {
  name: 'App',
  components: {
    MyFooter,
    MyHeader,
    MyList
}, 

data() {
      return {
        todos:
        [{id:"001",title:"学习vue",done:true},
        {id:"002",title:"看课程录播",done:false},
        {id:"003",title:"打游戏",done:false}]
      }
    },
methods: {
  receive(x){
    this.todos.unshift(x) //往data里的todos增加数据
  },
  checktodo(id){
    this.todos.forEach((todo)=>{
      if(todo.id==id) todo.done=!todo.done
    })
  },
  updateItem(id,title){
    this.todos.forEach((todo)=>{
      if(todo.id==id) todo.title=title
    })
  },
  deleteItem(id){
    this.todos=this.todos.filter((todo)=>{
      return todo.id!==id
    })
  },
  checkAll(done){
      this.todos.forEach(todo => {
        todo.done= done
      });
      
    },
    removeAllDone(){
      this.todos=this.todos.filter((todo)=>{
        return todo.done==false
      })
    }

},
mounted() {
  //绑定全局事件
  this.$bus.$on('checktodo',this.checktodo)
  this.$bus.$on('deleteItem',this.deleteItem)
  this.$bus.$on('updateItem',this.updateItem)
},
beforeDestroy(){
  //解绑
  this.$bus.$off('checktodo')
  this.$bus.$off('deleteItem')
  this.$bus.$off('updateItem')
}
}
</script>

