<template>
  <div class="todo-footer" v-show="todos.length">
            <label>
              <input type="checkbox" :checked="doneTotal===todos.length && todos.length>0 " @change="checkAllItem" />
            </label>
            <span>
              <span>已完成{{doneTotal}}</span> / 全部{{todos.length}}
            </span>
            <button class="btn btn-danger" @click="removeAll">清除已完成任务</button>
          </div>
</template>

<script>
export default {
  name: 'MyFooter',
  props:['todos','checkAll','removeAllDone'],
  computed:{
    doneTotal(){
      return this.todos.reduce((pre,current)=>{
        return pre+(current.done ? 1:0)
      },0)
    }
  },
  methods:{
    checkAllItem(e){
      this.checkAll(e.target.checked)
    },
    removeAll(){
      this.removeAllDone()
    }

  }
}
</script>

